import { RouterProvider, createHashRouter } from "react-router-dom";
import Home from "./views/Home";

const router = createHashRouter([
  {
    path: "/",
    element: <Home />,
    errorElement: <></>,
    children: [],
  },
]);

function App() {
  return (
    <div>
      <RouterProvider router={router} />
    </div>
  );
}

export default App;
