import { motion } from "framer-motion";
import styled from "styled-components";

export default function Icon(props: {
  icon: string;
  size?: string;
  color?: string;
}) {
  return (
    <>
      <span
        className="material-symbols-rounded"
        style={{
          color: props.color ?? "inherit",
          fontSize: props.size ?? "inherit",
        }}
      >
        {props.icon}
      </span>
    </>
  );
}
